import Button from '../Button';
import CourseCard from '../CourseCard';
import Search from '../Search';

function renderCourses(courses, authors, editHandler) {
  return courses.map((course) => {
    return <CourseCard
      course={ course }
      authors={ authors }
      key={ course.id }
      editCourse={ editHandler }
    />
  });
}

function Courses(props) {
  return (
    <>
      <div className="d-flex">
        <div className="p-2 w-50 flex-grow-1">
          <Search onChange={ props.onChange } />
        </div>
        <div className="d-flex align-items-end">
          <div className="p-2">
            <Button label="Add new course" className="btn btn-primary" onClick={ props.changeScreen } />
          </div>
        </div>
      </div>
      { renderCourses(props.courses, props.authors, props.editCourse) }
    </>
  );
}

export default Courses;