import Courses from './components/Courses';
import CreateCourse from './components/CreateCourse';
import Header from './components/Header';
import './App.css';
import React from 'react';
import { v4 as uuidv4 } from 'uuid';

const mockedCourseList = [
  {
    id: 'de5aaa59-90f5-4dbc-b8a9-aaf205c551ba',
    title: 'JavaScript',
    description: `Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged.`,
    creationDate: '8/3/2021',
    duration: 160,
    authors: ['27cc3006-e93a-4748-8ca8-73d06aa93b6d', 'f762978b-61eb-4096-812b-ebde22838167'],
  },
  {
    id: 'b5630fdd-7bf7-4d39-b75a-2b5906fd0916',
    title: 'Angular',
    description: `Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.`,
    creationDate: '10/11/2020',
    duration: 210,
    authors: ['df32994e-b23d-497c-9e4d-84e4dc02882f', '095a1817-d45b-4ed7-9cf7-b2417bcbf748'],
  }
];

const mockedAuthorsList = [
  {
    id: '27cc3006-e93a-4748-8ca8-73d06aa93b6d',
    name: 'Vasiliy Dobkin'
  },
  {
    id: 'f762978b-61eb-4096-812b-ebde22838167',
    name: 'Nicolas Kim'
  },
  {
    id: 'df32994e-b23d-497c-9e4d-84e4dc02882f',
    name: 'Anna Sidorenko'
  },
  {
    id: '095a1817-d45b-4ed7-9cf7-b2417bcbf748',
    name: 'Valentina Larina'
  }
];

function App() {
  const [authors, setAuthors] = React.useState(mockedAuthorsList);
  const [currentScreen, setCurrentScreen] = React.useState('courses');
  const [searchTerm, setSearchTerm] = React.useState('');
  const [searchResults, setSearchResults] = React.useState(mockedCourseList);
  const [newAuthorName, setNewAuthorName] = React.useState('');
  const [newCourse, setNewCourse] = React.useState(
    {
      'id': '',
      'title': '',
      'description': '',
      'creationDate': '',
      'duration': 0,
      'authors': []
    }
  );

  const handleChange = event => {
    setSearchTerm(event.target.value);
  };

  React.useEffect(() => {
    const results = mockedCourseList.filter(course =>
      course.title.toLowerCase().includes(searchTerm) ||
      course.id.toLowerCase().includes(searchTerm)
    );
    setSearchResults(results);
  }, [searchTerm]);

  const handleSetTitle = event => {
    setNewCourse({
      ...newCourse,
      title: event.target.value
    });
  };

  const handleSetDescription = event => {
    setNewCourse({
      ...newCourse,
      description: event.target.value
    });
  };

  const setAuthorName = event => {
    setNewAuthorName(event.target.value);
  };

  const saveNewAuthor = () => {
    if (!newAuthorName) {
      return;
    }
    setAuthors([
      ...authors,
      {id: uuidv4(), name: newAuthorName}
    ]);
    setNewAuthorName('');
  };
  
  const handleNewCourseAuthor = event => {
    setNewCourse({
      ...newCourse,
      authors: [event.target.dataset.author, ...newCourse.authors]
    });
  };

  const handleRemoveCourseAuthor = event => {
    setNewCourse({
      ...newCourse,
      authors: newCourse.authors.filter(authorId => { return authorId !== event.target.dataset.author })
    });
  };

  const handleSetDuration = event => {
    setNewCourse({
      ...newCourse,
      duration: event.target.value !== '' ? parseInt(event.target.value) : 0
    });
  }

  const handleSaveCourse = () => {
    const createdAt = new Date();
    mockedCourseList.push({
      ...newCourse,
      id: uuidv4(),
      creationDate: createdAt.getDate() + '/' + (createdAt.getMonth()+1) + '/' + createdAt.getFullYear()
    });
    setSearchResults(mockedCourseList);
    setCurrentScreen('courses');
  };

  const handleEditCourse = event => {
    setNewCourse(mockedCourseList.find(course => course.id === event.target.dataset.course));
    setCurrentScreen('edit');
  };

  const handleCancelCreate = () => {
    setCurrentScreen('courses');
  };

  return (
    <>
      <Header />
      {currentScreen === 'courses' ? <Courses
        courses={ searchResults }
        authors={ authors }
        currentSearch={ searchTerm }
        onChange={ handleChange }
        changeScreen={ setCurrentScreen }
        editCourse={ handleEditCourse }
      /> : <CreateCourse
        setTitle={ handleSetTitle }
        setDescription={ handleSetDescription }
        newCourse={ newCourse }
        availableAuthors={ authors }
        newAuthorName={ newAuthorName }
        setAuthorName={ setAuthorName }
        createNewAuthor={ saveNewAuthor }
        selectAuthor={ handleNewCourseAuthor }
        removeAuthor={ handleRemoveCourseAuthor }
        setDuration={ handleSetDuration }
        cancelCreate={ handleCancelCreate }
        saveCourse={ handleSaveCourse }
      />}
    </>
  );
}

export default App;
