const timeConvert = (n) => {
  if (isNaN(n)) {
    return '00:00';
  }
  let num = n;
  let hours = (num / 60);
  let rhours = Math.floor(hours);
  let minutes = (hours - rhours) * 60;
  let rminutes = Math.round(minutes);
  return rhours + ':' + String(rminutes).padStart(2, '0');
}

export default timeConvert;