import Button from '../Button';
import Input from '../Input';
import timeConvert from '../../helper/time';

function CreateCourse(props) {

  const listAvailableAuthors = (authors, selectedAuthors) => {
    selectedAuthors = selectedAuthors || [];
    let availableAuthors = authors.filter(author => 
      !selectedAuthors.some(selected => selected === author.id)
    );
    return availableAuthors.map((author) => {
      return(
        <div className="d-flex w-50 align-items-center" key={ author.id }>
          <div className="d-flex p-2 w-75">{ author.name }</div>
          <div className="d-flex p-2">
            <Button label="Add author" className="btn btn-success btn-sm m-2" data-author={ author.id } onClick={ props.selectAuthor } />
          </div>
        </div>
      );
    });
  };

  const listSelectedAuthors = (authorIds, authors) => {
    authorIds = authorIds || [];
    if (authorIds.length === 0) {
      return 'Author list is empty';
    }
    return authorIds.map(authorId => {
      let author = authors.find(author => author.id === authorId).name;
      return (
        <div className="d-flex w-50 align-items-center" key={ authorId }>
          <div className="p-2 w-75">{ author }</div>
          <div className="p-2">
            <Button label="Remove author" className="btn btn-danger btn-sm m-2" data-author={ authorId } onClick={ props.removeAuthor } />
          </div>
        </div>
      );
    });
  };

  return (
    <>
      <div className="d-flex">
        <div className="p-2 w-50 flex-grow-1">
          <Input label="Title" placeholder="Enter title..." value={ props.newCourse.title } onChange={ props.setTitle } />
        </div>
        <div className="d-flex align-items-end">
          <div className="p-2">
            <Button label="Create course" className="btn btn-primary" onClick={ props.saveCourse } />
            <Button label="Cancel" className="btn btn-link" onClick={ props.cancelCreate } />
          </div>
        </div>
      </div>
      <div className="d-flex">
        <div className="p-2 w-100">
          <Input label="Description" placeholder="Enter description" type="textarea" value={ props.newCourse.description } onChange={ props.setDescription } />
        </div>
      </div>
      <div className="d-flex">
        <div className="p-2 w-50 flex-grow-1">
          <div className="create_course--author">
            <h3 className="text-center">Add Author</h3>
            <div className="d-flex">
              <div class="w-50 flex-grow-1 pr-2">
                <span className="align-bottom">
                <Input label="Author name" placeholder="Enter author name..." value={ props.newAuthorName } onChange={ props.setAuthorName } />
                </span>
              </div>
              <div class="d-flex align-items-end">
                <div className="pl-2">
                  <Button label="Create author" className="btn btn-secondary" onClick={ props.createNewAuthor } />
                </div>
              </div>
            </div>
            <h3 className="text-center">Duration</h3> 
            <Input label="Duration" className="form-control mb-5" placeholder="Enter duration in minutes..." value={ props.newCourse.duration === 0 ? '' : props.newCourse.duration } onChange={ props.setDuration } />
            <h3>Duration: <strong>{ timeConvert(props.newCourse.duration) }</strong> hours</h3>
          </div>
        </div>
        <div className="p-2 w-50">
          <div className="create_course--author_list">
            <h3 className="text-center">Course authors</h3>
            <div className="d-flex flex-column align-items-center">
              { listAvailableAuthors(props.availableAuthors, props.newCourse.authors) }
            </div>
            <h3 className="text-center">Course authors</h3>
            <div className="d-flex flex-column align-items-center">
              { listSelectedAuthors(props.newCourse.authors, props.availableAuthors) }
            </div>
          </div>
        </div>
      </div>
    </>
  );

}

export default CreateCourse;