import Button from '../Button';
import './CourseCard.css';
import timeConvert from '../../helper/time';

function filterAuthors(authorIds, authors) {
  return authorIds.map(authorId => {
    return authors.find(author => author.id === authorId).name;
  }).join(', ');
}

function CourseCard(props) {
  const authorStyle = {
    maxWidth: 200
  }
  const authors = filterAuthors(props.course.authors, props.authors);
  return (
    <div className="d-flex border-bottom pb-4 m-2" id={ props.course.id }>
      <div className="p-2 w-75">
        <h2>{ props.course.title }</h2>
        <p className="text-secondary">{ props.course.description }</p>
      </div>
      <div className="p-2 w-25">
        <p><strong>Authors:</strong> <span className="d-inline-block text-truncate text-secondary align-bottom" style={ authorStyle } title={ authors }>{ authors }</span></p>
        <p><strong>Duration:</strong> <span className="text-secondary" title={ props.course.duration + ' minutes'}>{ timeConvert(props.course.duration) }</span></p>
        <p><strong>Created:</strong> <span className="text-secondary">{ props.course.creationDate }</span></p>
        <Button label="Show course" className="btn btn-success" data-course={ props.course.id } onClick={ props.editCourse } />
      </div>
    </div>
  );
}

export default CourseCard;